import { createAPI, metricsOptions } from '~/ui/utils/api';

export const metricSerpCholloApi = createAPI<
    any,
    { metricSerpToChollo(metricsData: metricsOptions['body']): Promise<void> }
>({
    name: 'metrics:serp:chollo',
    endpoint: 'metric/',
    methods: {
        async metricSerpToChollo(metricsData: metricsOptions['body']) {
            this.metric({
                body: {
                    name: metricsData.name,
                    data: metricsData.data,
                },
                method: 'POST',
            });
        },
    },
    read: () => {},
});
